@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=Inconsolata:wght@300&family=Merriweather+Sans:wght@500&family=Rajdhani&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
html{
    max-width: 1080px;
    margin: 0 auto;
    background-color: rgb(3, 18, 43);
}
h1 {
    color: white;
    font-family: 'Russo One', sans-serif;
    text-align: center;
    margin: 40px;
}
.article {
    border-radius: 10px 10px 0 0 ;
    margin: 0 auto;
    text-align: center;
    background: rgb(229,229,235);
    background: linear-gradient(90deg, rgba(229,229,235,1) 0%, rgba(255,255,255,1) 35%, rgba(215,215,215,1) 100%); 
}
.article-container{
    width: 300px;
    height: 400px;
    margin: 20px;
    color: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px;
    display: inline-block;
    text-align: center;
    overflow: hidden;
    background-color: white;
}
.source-date-container {
    width: 100%;
    display: block;
}

.article-source {
    font-size: 12px;
    font-family: 'Comfortaa', cursive;
    float: left;
    width: 50%;
    color: black;
}
.article-date {
    font-size: 12px;
    font-family: 'Comfortaa', cursive;
    float: right;
    width: 50%;
    color: black;
}
.article-image {
    float: left;
    width:  100%;
    height: 200px;
    object-fit: cover;
}
.article-title{
    color: black;
    font-family: 'Merriweather Sans', sans-serif;
}
.article-description {
    color: rgb(34, 34, 34);
    font-family: 'Rajdhani', sans-serif;
}
#hamburger {
    visibility: hidden;
}
.mobile-menu-li{
    display: none;
}
/* #mobile-menu-nav {
    position: relative;
    transform: translateX(-100vw);
    opacity: 0;
    z-index: 10;
    transition: transform .5s ease, opacity ease .2s;
} */

  #nav-menu.now-active {
    transform: translateX(0);
    transition: transform .5s ease, opacity ease .2s;
    opacity: 1;
    background-color:#eaeaea;
  }

  .nav-list {
    padding: 10px;
    list-style-type: none;
    font-size: 2em;
  }
.menu-container{
    width:20%;
    margin-right:2%;
    float: left;
}

.clearfix{
    clear:both
}
#logo {
    width: 125px;
}
ul {
    margin-top: 2em;
    margin-right: 20px;
}
li {
    font-family: 'Oswald', sans-serif;
    display: inline;
    padding: 15px;
    font-size: 25px;
    text-align: right;
    transition: .6s
}
a:hover {
    color: white;
}
a:active {
    color: #66fcf1;
}
a {
    text-decoration: none;
    color: #66fcf1;
    transition: .6s
}

@media only screen and (max-width: 600px) {
    .main-menu-nav {
      display: none;
    }
    #hamburger {
        visibility: visible;
        margin-right: 10px;
        margin-top: 25px;
        display: block;
        width: 45px;
        height: 30px;
        position: relative;
        cursor: pointer;
        float: right;
    }
      #hamburger span {
        display: block;
        position: absolute;
        height: 6px;
        width: 100%;
        background:#66fcf1;
        border-radius: 1px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: .5s ease-in-out;
      }
    
      #hamburger span:nth-child(1) {
        top: 0px;
        transform-origin: left center;
      }
    
      #hamburger span:nth-child(2) {
        top: 21px;
        transform-origin: left center;
      }
    
      #hamburger span:nth-child(3) {
        top: 42px;
        transform-origin: left center;
      }
    
      #hamburger.close span:nth-child(1) {
        transform: rotate(45deg);
      }
    
      #hamburger.close span:nth-child(2) {
        width: 0%;
        opacity: 0;
        transition: .1s;
      }
    
      #hamburger.close span:nth-child(3) {
        transform: rotate(-45deg);
      }

    .mobile-menu-li{
        display: block;
        text-align: center;
    }
    .mobile-menu-li::first-line{
        margin-top: 10em;
    }
    .mobile-menu-ul {
        padding: 0;
        margin: 0;
    }
  }
